import type { IDBPDatabase } from "idb";
import { openDB } from "idb";
import ReferencesCache from "./modules/references";
import SessionCache from "./modules/session";

const cache = new (class Cache {
  private DB_VERSION = 1;
  private DB_NAME = "cache";
  private db?: IDBPDatabase;

  references!: ReferencesCache;
  session!: SessionCache;

  async init() {
    this.db = await openDB(this.DB_NAME, this.DB_VERSION, {
      upgrade(db) {
        ReferencesCache.init(db);
        SessionCache.init(db);
      },
    });
    this.references = new ReferencesCache(this.db);
    this.session = new SessionCache(this.db);
  }
})();

export default cache;
