import cache from "@/cache/cache";
import { ServiceModule } from "@/services";
import type {
  DisconnectPayload,
  DisconnectServerResponse,
  IsConnectedPayload,
  IsConnectedServerResponse,
  LoginPayload,
  LoginServerResponse,
} from "@/types/service";
import type { SessionCacheValue, SessionState } from "@/types/session";
import { SESSION_CODE_HEADER } from "@/utils/constants";
import { currentTimestamp } from "@/utils/time";
import { defineStore } from "pinia";

const useSessionStore = defineStore("session", {
  state: () =>
    ({
      user: undefined,
      sessionCode: "",
      isConnected: false,
    } as SessionState),
  getters: {},
  actions: {
    clearData() {
      this.sessionCode = "";
      this.user = undefined;
      this.isConnected = false;
      cache.session.deleteValue("session").then();
    },
    async login(
      email: string,
      password: string,
      rememberSession?: boolean
    ): Promise<void> {
      const payload = { email, password } as LoginPayload;
      const response = await ServiceModule.postData<LoginServerResponse>(
        "login",
        payload
      );
      if (response && response.sessionCode && response.user) {
        if (rememberSession) {
          cache.session.saveValue("session", {
            user: response.user,
            sessionCode: response.sessionCode,
          } as SessionCacheValue);
        } else {
          cache.session.deleteValue("session");
        }
        this.user = response.user;
        this.sessionCode = response.sessionCode;
        this.isConnected = true;
      } else {
        //TODO Handle server error
      }
    },
    async fetchIsConnected() {
      const cacheValue = await cache.session.getValue("session");
      const now = currentTimestamp();
      if (!cacheValue || cacheValue.expires < now) {
        this.clearData();
        return;
      }
      const response = await ServiceModule.postData<IsConnectedServerResponse>(
        "isConnected",
        {
          sessionCode: cacheValue.value.sessionCode,
        } as IsConnectedPayload
      );
      if (response?.active) {
        this.sessionCode = cacheValue.value.sessionCode;
        this.user = cacheValue.value.user;
        this.isConnected = true;
        if (this.sessionCode)
          ServiceModule.setHeader(SESSION_CODE_HEADER, this.sessionCode);
      } else {
        this.clearData();
      }
    },
    async disconnect() {
      if (this.sessionCode) {
        const response = await ServiceModule.postData<DisconnectServerResponse>(
          "disconnect",
          { sessionCode: this.sessionCode } as DisconnectPayload
        );
        if (response && response.status) {
          this.clearData();
        }
      }
    },
  },
});

export default useSessionStore;
