import type { CacheValue } from "@/types/cache";
import { timestampFromNow } from "@/utils/time";
import type { IDBPDatabase } from "idb";

export default abstract class CacheModule<K extends IDBValidKey, V> {
  protected storeName: string;
  protected cache: IDBPDatabase;
  protected duration: number;

  constructor(sn: string, c: IDBPDatabase, duration: number) {
    this.storeName = sn;
    this.cache = c;
    this.duration = duration;
  }

  getValue(k: K): Promise<CacheValue<K, V> | undefined> {
    return this.cache.get(this.storeName, k);
  }

  getAll(): Promise<CacheValue<K, V>[]> {
    return this.cache.getAll(this.storeName);
  }

  saveValue(k: K, v: V): Promise<IDBValidKey | IDBKeyRange | undefined> {
    const value = {
      key: k,
      expires: timestampFromNow(this.duration),
      value: v,
    } as CacheValue<K, V>;
    return this.cache.put(this.storeName, value, k);
  }

  deleteValue(k: K): Promise<void> {
    return this.cache.delete(this.storeName, k);
  }
}
