import type { ServerResponse } from "@/types/service";
import type { AxiosResponse } from "axios";
import axios from "axios";

export abstract class ServiceModule {
  static #server = axios.create({
    baseURL: process.env.VUE_APP_BACK_END_SERVER_URL,
    validateStatus: () => true,
  });

  static setHeader(key: string, value: string) {
    this.#server.defaults.headers.common[key] = value;
  }

  static handleResponse<T = unknown>(
    response: AxiosResponse<ServerResponse<T>>
  ): T | undefined {
    if (response.data.errorCode) {
      //TODO Handle error code
      console.error(response.data.errorCode, response.data.errorMessage);
    }
    return response.data.data;
  }

  static async postData<T = unknown>(
    url: string,
    data?: unknown,
    queryArgs?: Record<string, unknown>
  ): Promise<T | undefined> {
    const response = await this.#server.post<ServerResponse<T>>(url, data, {
      params: queryArgs,
    });
    if (response && response.data) return this.handleResponse(response);
  }

  static async getData<T = unknown>(
    url: string,
    queryArgs?: Record<string, unknown>
  ): Promise<T | undefined> {
    const response = await this.#server.get<ServerResponse<T>>(url, {
      params: queryArgs,
    });
    if (response && response.data) return this.handleResponse(response);
  }
}
