import type { SessionCacheValue } from "@/types/session";
import type { IDBPDatabase } from "idb";
import CacheModule from "../cache-module";

export default class SessionCache extends CacheModule<
  string,
  SessionCacheValue
> {
  constructor(cache: IDBPDatabase) {
    super("session", cache, 14400);
  }

  static init(db: IDBPDatabase) {
    db.createObjectStore("session");
  }
}
