import { ServiceModule } from "@/services";
import type { Reference } from "@/types/references";
import { currentTimestamp } from "@/utils/time";
import type { IDBPDatabase } from "idb";
import CacheModule from "../cache-module";

export default class ReferencesCache extends CacheModule<string, Reference> {
  constructor(cache: IDBPDatabase) {
    super("references", cache, 1440);
  }

  static init(db: IDBPDatabase) {
    db.createObjectStore("references");
  }

  async getReference(tableName: string): Promise<Reference | undefined> {
    const cacheValue = await this.getValue(tableName);
    if (cacheValue && cacheValue.expires > currentTimestamp()) {
      return cacheValue.value;
    }
    const serviceValue = await ServiceModule.getData<Reference>("references", {
      tableName,
    });
    if (serviceValue) {
      this.saveValue(tableName, serviceValue).then();
      return serviceValue;
    }
    return undefined;
  }
}
